<template>
  <Toolbar class="navbar">
    <template #left>
      <h2>2</h2>
    </template>
  </Toolbar>
  <Toast position="bottom-left" />
  <div class="root">
    <div class="container">
      <Card class="card">
        <template #title>
          <h4 class="title"><i class="pi pi-question-circle titleicon" />{{ questions[curQues].question }}</h4>
        </template>
        <template #content>
          <div class="radbtns">
            <div 
              v-for="option of questions[curQues].choices" 
              :key="option[0]" 
              class="p-field-radiobutton"
            >
                <RadioButton 
                  :id="option[0]" 
                  :name="option[0]"
                  :value="option[0]" 
                  v-model="questions[curQues].choice" 
                  class="option"
                  @change="check"
                />
                <label :for="option[0]">{{ option }}</label>
            </div>
          </div>
        </template>
        <template #footer>
          <div class="footer">
            <Paginator
              rows="1"
              :totalRecords="questions.length"
              :template="'PrevPageLink CurrentPageReport NextPageLink'"
              @page="onPageChange($event)"
              class="paginator"
            />
          </div>
        </template>
      </Card>
      <Button 
        id="submitbtn"
        class="submitbtn"
        :style="{ display: curQues === questions.length - 1 ? 'block' : 'none' }"
        type="submit" 
        label="" 
        @click="handleSubmitBtnClick"
      />
      <Dialog header="Oops!" v-model:visible="showUnfinishedDialog" modal>
        
        <template #footer>
          <Button label="OK" @click="showUnfinishedDialog = false" />
        </template>
      </Dialog>
      <Dialog header="?" v-model:visible="showSubmitDialog" modal>
        
        <template #footer>
          <Button label="" icon="pi pi-times" @click="showSubmitDialog = false" />
          <Button label="" icon="pi pi-check" @click="handleSubmit" />
        </template>
      </Dialog>
      {{ window.width }}
    </div>
  </div>
</template>

<script>
import { user, setUser } from '../main';
import { storeUser } from '../api';

export default {
  name: 'Questions',
  created() {
      window.addEventListener('resize', this.handleResize);
      this.handleResize();
  },
  destroyed() {
      window.removeEventListener('resize', this.handleResize);
  },
  data() {
    return {
      user,
      questions: [
        { question: '1. : 603', answer: 'O', choices: ['O', 'X'], choice: '' }, 
        { question: '2. ', answer: 'O', choices: ['O', 'X'], choice: '' }, 
        { question: '3. ', answer: 'O', choices: ['O', 'X'], choice: '' }, 
        { question: '4. ', answer: 'O', choices: ['O', 'X'], choice: '' }, 
        { question: '5. ', answer: 'O', choices: ['O', 'X'], choice: '' }, 
        { question: '6. :', answer: '2', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '7. :', answer: '2', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '8. :', answer: '1', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '9. :', answer: '1', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '10. ?', answer: '2', choices: ['1: 1000', '2: 500', '3: 300'], choice: '' }, 
        { question: '11. :', answer: '2', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '12. :', answer: '1', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '13. :', answer: '2', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '14. :', answer: '2', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '15. :', answer: '3', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '16. :', answer: '3', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '17. :', answer: '1', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '18. :', answer: '2', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '19. :', answer: '3', choices: ['1: ', '2: ', '3: '], choice: '' }, 
        { question: '20. :', answer: '1', choices: ['1: ', '2: ', '3: '], choice: '' }, 
      ],
      curQues: 0,
      showUnfinishedDialog: false,
      showSubmitDialog: false,
      window: {
          width: 0,
          height: 0
      }
    };
  },
  methods: {
    onPageChange(event) {
      this.curQues = event.page;
    },
    check() {
      this.$toast.add((this.questions[this.curQues].choice === this.questions[this.curQues].answer) ? 
        {severity:'success', summary: '!', life: 1000} : 
        {severity:'error', summary: '!', life: 1000} );
    },
    handleSubmitBtnClick() {
      for (let i = 0; i < this.questions.length; i++) {
        if (!this.questions[i].choice) {
          this.showUnfinishedDialog = true;
          return;
        }
      }
      this.showSubmitDialog = true;
    },
    async handleSubmit() {
      let grade = 0;
      this.questions.forEach(question => {
        if (question.choice === question.answer) grade += 5;
      });

      try {
        setUser({
          name: this.user.name,
          stuid: this.user.stuid,
          depGrade: this.user.depGrade,
          grade,
        });
        await storeUser({ user: this.user });

        this.$router.push('/result');
      } catch (error) {
        console.log(error);
      }

      this.showSubmitDialog = false;
    },
    handleResize() {
        this.window.width = window.innerWidth;
        this.window.height = window.innerHeight;
    },
  },
};
</script>

